<template>
  <docs-page>
      <code-block title="" :code="baseCode" desc="textarea ">
          <base-demo></base-demo>
      </code-block>
  </docs-page>
</template>
<script>
import baseDemo from './base'
import baseCode from '!raw-loader!./base'

export default {
    components: {
        baseDemo
    },
    data() {
        return {
            baseCode
        }
    }
}
</script>



